.gpt_features_container_feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  margin: 1rem;
}

.gpt_features_container_feature_title {
  flex: 1;
  max-width: 180px;
  margin-right: 2rem;
}

.gpt_features_container_feature_title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04;
  color: #fff;
}


.gpt_features_container_feature_title div{
  width: 38px;
  height: 5px;
  background: var(--gradient-bar);
  box-shadow: 0px 4px 4px rgba(0,0,0,0.25);
  margin-bottom: 0.25rem;
}

.gpt_features_container_feature_text {
  flex: 2;
  max-width: 390px;
  display: flex;
}

.gpt_features_container_feature_text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-text);
}

@media screen and (max-width: 550px) {
  .gpt_features_container_feature_title h1 {
    font-size: 14px;
    line-height: 22px;
  }

  .gpt_features_container_feature_title p {
    font-size: 12px;
    line-height: 20px;
  }
  
  .gpt_features_container_feature {
    margin: 1rem 0;
  }
}